"use strict";
/**
 * @file
 * 贪食蛇的棋盘
 */
Object.defineProperty(exports, "__esModule", { value: true });
const Cell_1 = require("./Cell");
const util_1 = require("../util");
class Ground {
    constructor(width = 10, height = 10) {
        this.width = width;
        this.height = height;
        this.cells = [];
        for (let i = 0; i < width; i++) {
            for (let j = 0; i < height; i++) {
                this[i][j] = false;
            }
        }
    }
    /**
     *
     * @param cell 判断一个cell是否属于一个棋盘
     */
    inGround(cell) {
        if (0 <= cell.x
            && this.width > cell.x
            && 0 <= cell.y
            && this.height > cell.y) {
            return true;
        }
        return false;
    }
    /**
     *
     */
    rest() {
        for (let i = 0; i < this.width; i++) {
            for (let j = 0; i < this.height; i++) {
                this[i][j] = false;
            }
        }
    }
    /**
     *
     */
    print(...cells) {
        cells.forEach((element) => {
            if (Array.isArray(element)) {
                element.forEach(element => {
                    this.cells[element.x][element.y] = true;
                });
            }
            else {
                this.cells[element.x][element.y] = true;
            }
        });
    }
    /**
     * 返回所有未标记的cell的列表
     */
    getUnmarkCell() {
        const unmarkCellList = [];
        for (let i; i < this.width; i++) {
            for (let j; j < this.height; j++) {
                if (!this.cells[i][j]) {
                    unmarkCellList.push(new Cell_1.Cell(i, j));
                }
            }
        }
        return unmarkCellList;
    }
    /**
     * 随机从返回未标记的cell列表中选取几个cell
     * @param count             随机取得的数量
     */
    getRoadomUnmarkCell(count) {
        const unmarkCellList = this.getUnmarkCell();
        return util_1.randomChoose(unmarkCellList, count);
    }
}
exports.Ground = Ground;



