"use strict";
/**
 * @file
 * 贪食蛇游戏
 */
Object.defineProperty(exports, "__esModule", { value: true });
const Snake_1 = require("./Snake");
const Ground_1 = require("./Ground");
const Direction_1 = require("./Direction");
const Food_1 = require("./Food");
class Game {
    constructor({ width = 10, height = 10, headerPostion, direction, bodyLength = 3, }) {
        //初始化方向
        if (direction) {
            direction = Direction_1.Direction.Up;
        }
        //设置头部的默认值，方向默认值
        if (headerPostion) {
            headerPostion = {
                x: Math.floor(width / 2),
                y: Math.floor(height / 2),
            };
        }
        //创建蛇
        this.snake = new Snake_1.Snake({
            headerPostion: headerPostion,
            direction: direction,
            bodyLength: bodyLength,
        });
        //创建棋盘
        this.ground = new Ground_1.Ground(width, height);
    }
    /**
     * 生成事物
     */
    makeFoot() {
        const cell = this.ground.getRoadomUnmarkCell(1)[0];
        this.foods.push(new Food_1.Food(cell.x, cell.y, 1));
    }
    /**
     * 一帧
     */
    frame() {
        this.ground.rest();
        this.ground.print(this.foods, this.snake.header, this.snake.body);
        var eatFood = this.snake.move(this.foods);
        if (eatFood) {
            this.foods = this.foods.filter(item => item != eatFood);
            const cell = this.ground.getRoadomUnmarkCell(1)[0];
            this.foods.push(new Food_1.Food(cell.x, cell.y, 1));
        }
    }
}
exports.Game = Game;



